<template>
    <div class="main_view">
        <RouterView
            v-if="!loading"
            :started="this.started"
            :turn="turn"
            @end_turn="turn = false"
        />
        <LoaderComponent v-else />
    </div>
</template>
<script>
import LoaderComponent from "@/components/LoaderComponent.vue";
import socketIO from "@/socket";

export default {
    created() {
        this.socket = socketIO;
        this.socket.on("waiting_other", () => {
            this.loading = true;
        });
        this.socket.on("game_ready", () => {
            this.loading = false;
            this.started = true;
        });
        this.socket.on("your_turn", () => {
            this.turn = true;
        });
    },
    components: {
        LoaderComponent,
    },
    data() {
        return {
            loading: false,
            socket: null,
            started: false,
            turn: false,
        };
    },
    name: "GameView",
};
</script>
<style lang="scss" scoped>
.main_view {
    background-color: #252525;
    height: 100%;
    width: 100%;
}
</style>
